document.addEventListener("DOMContentLoaded", () => {
	// Includes
	// // scrollLockIOS
// Iphones
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
}

// Слушатель на один элемент
let scrollLock_BtnListener = (item) => {
	item.addEventListener('click', (e) => {
		item.classList.toggle('scroll')
		if (item.classList.contains('scroll')) {
			disableScroll();
		} else {
			enableScroll();
		}
	});
};
	// // Animate In Scroll
// class="animate-item" data-animation='from-left' data-animation-once
const animItems = document.querySelectorAll(".animate-item");

const elementInViewport = (element, offsetTop, offsetBottom) => {
	let bounds = element.getBoundingClientRect();
	if ((bounds.top + bounds.height - bounds.height * offsetBottom > 0) && (window.innerHeight - bounds.top - bounds.height * offsetTop > 0)) {
		element.classList.add("animated");
	} else if (!element.hasAttribute("data-animation-once")) {
		element.classList.remove("animated");
	}
};

if (animItems.length != 0) {
	animItems.forEach(el => {
		elementInViewport(el, .5, .5);
	})
	document.addEventListener('scroll', () => {
		animItems.forEach(el => {
			elementInViewport(el, .5, .5);
		});
	});
};
// -- //

/*
// OLD
const scrollAnimate = () => {
	// let windowCenter = (window.innerHeight / 2) + window.scrollY;
	let windowBottom = window.innerHeight + window.scrollY - 300;
	animItems.forEach((el) => {
		let scrollOffset = el.offsetTop;
		if (windowBottom >= scrollOffset) {
			el.classList.add('animated')
		} else {
			let animationRepeat = el.hasAttribute('data-animation-once');
			if (!animationRepeat) {
				el.classList.remove('animated')
			}
		}
	});
}

if (animItems.length != 0) {
	console.log('@ANIMATEONSCROLL: ON')
	scrollAnimate()
	window.addEventListener('scroll', () => {
		scrollAnimate()

	})
} else {
	console.log('@ANIMATEONSCROLL: "На странице нету елементов анимации, НО функция scrollAnimate включена! Возможно вам стоит её отключить?"')
}
*/;
	// // MODAL

// vars
const body = document.querySelector('body');
const modalButtons = document.querySelectorAll('.modal-btn');
const lockPadding = document.querySelectorAll('.fixed-padding');
const modalCloseButtons = document.querySelectorAll('.close-modal');
const timeout = 800;
let currentCloseBtn;
let unlock = true;
let bodyWasNotLock;

// Проверки
if (lockPadding.length === 0) {
	console.log('@MODALS: "Класс "fixed-padding" нигде не был указан!"')
}
if (typeof (disableScroll) !== 'function' && typeof (enableScroll) !== 'function') {
	console.log(`Тип данных переменных "disableScroll" и "enableScroll": "${typeof (disableScroll)} и ${typeof (enableScroll)}"! Для IOS НЕ будет выполнятся скрипт scrollLockIOS`);
}
// -- //
if (modalButtons.length > 0) {
	for (let index = 0; index < modalButtons.length; index++) {
		const modalCurrentButton = modalButtons[index];
		modalCurrentButton.addEventListener("click", function (e) {
			const modalName = modalCurrentButton.dataset.path;
			const curentModal = document.getElementById(modalName);
			if (curentModal != null) {
				modalOpen(curentModal);
				setTimeout(() => {
					modalCloseButtons.forEach((el) => {
						el.focus()
					})
				}, 100)
				e.preventDefault();
			} else {
				console.log(`Модальное окно не существует! ${curentModal}`);
			}
		});
	}
}

if (modalCloseButtons.length > 0) {
	for (let index = 0; index < modalCloseButtons.length; index++) {
		const el = modalCloseButtons[index];
		currentCloseBtn = el;
		el.addEventListener('click', function (e) {
			modalClose(el.closest('.modal'));
			e.preventDefault();
		});
	}
}
let previousActiveElement;
function modalOpen(curentModal) {
	if (curentModal && unlock) {
		previousActiveElement = document.activeElement;
		const modalActive = document.querySelector('.modal.open');
		if (modalActive) {
			modalClose(modalActive, false);
		}
		else {
			bodyLock();
		}
		curentModal.classList.add('open');
		curentModal.addEventListener("mousedown", function (e) {
			if (!e.target.closest('.modal__content')) {
				modalClose(e.target.closest('.modal'));
			}
		});
	}
}
function modalClose(modalActive, doUnlock = true) {
	if (unlock) {
		modalActive.classList.remove('open');
		if (doUnlock) {
			bodyUnlock();
		}
	}
	setTimeout(function () {
		previousActiveElement.focus();
	}, timeout);

}

function bodyLock() {
	const lockPaddingValue = window.innerWidth - body.offsetWidth + 'px';
	if (lockPadding.length > 0) {
		for (let index = 0; index < lockPadding.length; index++) {
			const el = lockPadding[index];
			el.style.paddingRight = lockPaddingValue;
		}
	}
	body.style.paddingRight = lockPaddingValue;
	if (!body.classList.contains('lock')) {
		bodyWasNotLock = true;
		if (typeof (disableScroll) === "function") {

			if (isiPhone || isiPad || isiPod) {
				disableScroll();
			} else {
				body.classList.add('lock');
			}
		} else {
			body.classList.add('lock');
		}
	} else {
		bodyWasNotLock = false;
	}
	unlock = false;
	setTimeout(function () {
		unlock = true;
	}, timeout);
}

function bodyUnlock() {
	setTimeout(function () {
		if (lockPadding.length > 0) {
			for (let index = 0; index < lockPadding.length; index++) {
				const el = lockPadding[index];
				el.style.paddingRight = '0px';
			}
		}
		body.style.paddingRight = '0px';
		if (bodyWasNotLock == true) {
			if (typeof (enableScroll) === "function") {
				if (isiPhone || isiPad || isiPod) {
					enableScroll();
				} else {
					body.classList.remove('lock');
				}
			} else {

				body.classList.remove('lock');
			}
		}
	}, timeout);
}

document.addEventListener('keydown', function (e) {
	if (document.querySelector('.modal.open')) {
		if (e.which === 27) {
			const modalActive = document.querySelector('.modal.open');
			modalClose(modalActive);
		}
	}
});
(function () {
	if (!Element.prototype.closest) {
		Element.prototype.closest = function (css) {
			var node = this;
			while (node) {
				if (node.matches(css)) return node;
				else node = node.parentElement;
			}
			return null;
		};
	}
})();
(function () {
	if (!Element.prototype.matches) {
		Element.prototype.matches = Element.prototype.matchesSelector ||
			Element.prototype.webkitMatchesSelector ||
			Element.prototype.mozMatchesSelector ||
			Element.prototype.msMatchesSelector;
	}
})();;
	// // COUNTER
// Тегу указать атрибут "data-count="число"".
// Перед скриптом должен быть подключен animateOnScroll!
// counter(елементы где отображать счёт, элемент animateOnScroll (trigger), длительность, шаг)
// counter(document.querySelectorAll('.big'), document.querySelector('#hero4'));

const counter = (elements, trigger, countDuration = 2000, countStep = 1) => {
	if (trigger == 'auto' || trigger == null || trigger == undefined) {
		console.log(`Animated Item не указан! Проверьте подключен ли "animateOnScroll"! ${animatedItem}`)
	} else {
		function countNumbers(elements) {
			elements.forEach((el) => {
				let n = 0;
				let num = el.getAttribute("data-count");
				let t = (countDuration / (num / countStep));
				let interval = setInterval(() => {
					n = n + countStep;
					if (n >= num) {
						clearInterval(interval);
					}
					el.innerHTML = n;
				}, t);
			});
		}
		window.addEventListener('scroll', function scrolling() {
			if (trigger.classList.contains('animated')) {
				this.removeEventListener('scroll', scrolling);
				countNumbers(elements);
			}
		});
	}
};
	// -- //
})

